import img1 from "../image/galery1.jpg";
import img2 from "../image/galery2.jpg";
import img3 from "../image/galery3.jpg";
import img4 from "../image/galery4.jpg";
import img5 from "../image/galery5.jpg";
import img6 from "../image/galery6.jpg";
import img7 from "../image/galery7.jpg";
import img8 from "../image/galery8.jpg";
import img9 from "../image/galery9.jpg";
import img10 from "../image/galery10.jpg";
import img11 from "../image/galery11.jpg";
import img12 from "../image/galery12.jpg";
import { Image, Col, Row, Typography } from "antd";
const { Title } = Typography;
export const Galery = () => {
  const image = [img1, img2, img3, img4, img5, img6, img7, img8, img9, img10, img12];
  return (
    <section id="galery">
      <Row justify="center" className="galery">
        <Col md={18}>
          <Title className="title-galery">Нашь зал</Title>
          <div className="images">
            {image.map((item, i) => (
              <div key={i}>
                <Image src={item} alt={"galery" + i} width="100%" />
              </div>
            ))}
          </div>
        </Col>
      </Row>
    </section>
  );
};
