import { Col, Row } from "antd";
import React, { useState } from "react";
import img2 from "../image/slider1.jpg";
import img1 from "../image/img3.jpg";
import img4 from "../image/img5.png";
export const Service = () => {
  const [slid, setSlid] = useState(0);
  const slides = [img1, img2, img4];

  return (
    <section className="service d-flex align-items-center" id="service">
      <Row justify="center">
        <Col md={18}>
          <div className="d-flex" style={{ marginBottom: 25 }}>
            <h3 className="h1 m-0 title-service">Выбери свое направление и начинай уже сейчас!</h3>
          </div>
          <Row gutter={10}>
            <Col md={16}>
              <img src={slides[slid]} alt="" className="w-100" />
            </Col>
            <Col md={8}>
              <ul>
                <li onClick={() => setSlid(0)}>
                  <span className="title">Тренажерный зал</span> <br />
                  Lorem ipsum dolor sit amet consectetur adipisicing elit. Neque aperiam repudiandae dicta, reiciendis dolorum nemo quidem eius,
                </li>
                <li onClick={() => setSlid(1)}>
                  <span className="title">Групповые тренировки</span>
                  <br />
                  Lorem ipsum dolor sit amet consectetur adipisicing elit. Neque aperiam repudiandae dicta, reiciendis dolorum nemo quidem eius,
                </li>
                <li onClick={() => setSlid(2)}>
                  <span className="title">Персональные занятия</span>
                  <br />
                  Lorem ipsum dolor sit amet consectetur adipisicing elit. Neque aperiam repudiandae dicta, reiciendis dolorum nemo quidem eius,
                </li>
              </ul>
            </Col>
          </Row>
        </Col>
      </Row>
    </section>
  );
};
