import { Row, Col, Typography, Grid } from "antd";
import img from "../image/author3.jpg";
export const Author = () => {
  const { md } = Grid.useBreakpoint();
  return (
    <section id="author" style={{ background: "#02c9d0" }}>
      <Row justify="center" align="middle" style={{ height: "100%" }}>
        <Col span={18}>
          <Row align="middle">
            <Col md={13} style={{ textAlign: "end" }}>
              <Typography.Title style={{ color: "#ffffff" }}>
                <span style={{ color: "#e6e6e6" }}>Автор дипломной работы:</span> Суюнбаев Асет
              </Typography.Title>
              <Typography.Title style={{ color: "#ffffff" }} level={3}>
                <span style={{ color: "#e6e6e6" }}>Тема дипломной работы:</span> Веб-Сайта для "Спортивного клуба"
              </Typography.Title>
              <Typography.Title style={{ color: "#ffffff" }} level={3}>
                <span style={{ color: "#e6e6e6" }}>Студент группы:</span> 4ПО-321
              </Typography.Title>
            </Col>
            <Col md={11} style={{ display: md ? "block" : "none" }}>
              <img src={img} alt="author" />
            </Col>
          </Row>
        </Col>
      </Row>
    </section>
  );
};
