import { Row, Col, Button } from "antd";
export const Subscription = () => {
  return (
    <section id="subscription" className="subscription">
      <Row justify="center" style={{ height: "100%", padding: "50px 0" }}>
        <Col md={18}>
          <Row justify="center" align="middle" style={{ height: "100%", gap: 100 }}>
            <Col span={24}>
              <h1 className="title-subscription">Выбери свой путь</h1>
            </Col>
            <Col span={24} md={4} className="card">
              <p className="title">Новичек</p>
              <p>4 занятия - 5000 тенге</p>
              <Button>
                <a href="#feedback">Купить абонемент</a>
              </Button>
            </Col>
            <Col span={24} md={4} className="card">
              <p className="title">Любитель</p>
              <p>8 занятия - 8000 тенге</p>
              <Button>
                <a href="#feedback">Купить абонемент</a>
              </Button>
            </Col>
            <Col span={24} md={4} className="card">
              <p className="title">Профи</p>
              <p>12 занятия - 14000 тенге</p>
              <Button>
                <a href="#feedback">Купить абонемент</a>
              </Button>
            </Col>
          </Row>
        </Col>
      </Row>
    </section>
  );
};
