import { Button, Drawer, Grid } from "antd";
import { MenuOutlined } from "@ant-design/icons";
import { useState } from "react";
export const Header = () => {
  const anchors = document.querySelectorAll('a[href*="#"]');
  const { md } = Grid.useBreakpoint();

  for (let anchor of anchors) {
    anchor.addEventListener("click", function (e) {
      e.preventDefault();
      const blockID = anchor.getAttribute("href").substr(1);
      document.getElementById(blockID).scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    });
  }
  return (
    <header id="header" className="header">
      {md ? <Desktop /> : <Mobile />}
    </header>
  );
};
const Mobile = () => {
  const [visible, setVisible] = useState(false);
  const anchors = document.querySelectorAll('a[href*="#"]');
  for (let i of anchors) {
    i.addEventListener("click", () => setVisible(false));
  }
  return (
    <div style={{ display: "flex", justifyContent: "space-between", padding: "5px 10px" }}>
      <a href="#home" style={{ fontSize: 20, color: "#fff" }}>
        GoodFit
      </a>
      <Button style={{ background: "black", color: "#fff" }} onClick={() => setVisible(true)}>
        <MenuOutlined />
      </Button>
      <Drawer className="nav-mobile" placement="left" title="Меню" visible={visible} onClose={() => setVisible(false)}>
        <ul>
          <li>
            <a href="#home" style={{ fontSize: 20 }}>
              GoodFit
            </a>
          </li>
          <li>
            <a href="#service">Услуги</a>
          </li>
          <li>
            <a href="#galery">Галерея</a>
          </li>
          <li>
            <a href="#about">О нас</a>
          </li>
          <li>
            <a href="#subscription">Абонемент</a>
          </li>
          <li>
            <a href="#feedback">Заказать звонок</a>
          </li>
          <li>
            <a href="#author">Автор</a>
          </li>
          <li>
            <a href="#home">
              <i className="bi bi-instagram"></i>
              Наш инстраграм
            </a>
          </li>
          <li>
            <a href="#home">
              <i className="bi bi-whatsapp"></i>
              Тех поддержка
            </a>
          </li>
          <li>
            <a href="#home">
              <i className="bi bi-telegram"></i>
              Менеджер
            </a>
          </li>
          <li>
            <a href="#home">+7(700)689-69-45</a>
          </li>
        </ul>
      </Drawer>
    </div>
  );
};
