import { Button, Modal, Col, Form, Input, InputNumber, Row, Space } from "antd";
import { useState } from "react";
export const FeedBack = () => {
  const [state, setState] = useState(false);

  const onFinish = (values) => {
    fetch("/", {
      method: "POST",
      body: JSON.stringify(values),
      headers: {
        "content-type": "application/json",
      },
    }).then((res) => {
      setState(true);
      setTimeout(() => setState(false), 5000);
    });
  };
  return (
    <section id="feedback" className="feedback">
      <Modal footer={false} visible={state} style={{ textAlign: "center" }}>
        <h1>Ваша заявка отправленна</h1> <h2>наш менеджер свяжется с вами течение 15 минут</h2>
      </Modal>
      <Row justify="center">
        <Col md={18}>
          <Row justify="center">
            <Col span={24}>
              <h1 className="title" align="center">
                Тренажерный зал получите консультация
              </h1>
            </Col>
            <Col md={10}>
              <Form size="large" onFinish={onFinish}>
                <Space style={{ width: "100%" }}>
                  <Form.Item name="email">
                    <Input placeholder="Ваша почта" />
                  </Form.Item>
                  <Form.Item name="phone">
                    <InputNumber placeholder="Вашь телефон" style={{ width: "100%" }} type="tel" />
                  </Form.Item>
                  <Form.Item>
                    <Button htmlType="submit" style={{ width: "100%", background: "#149fb8", borderColor: "#149fb8", borderRadius: 10, color: "#fff" }}>
                      Отправить
                    </Button>
                  </Form.Item>
                </Space>
              </Form>
            </Col>
          </Row>
        </Col>
      </Row>
    </section>
  );
};
