import { Col, Row, Carousel, Grid } from "antd";
import slide1 from "../image/slider1.jpg";
import slide2 from "../image/slider2.jpg";
import slide3 from "../image/slider3.jpg";
import { useRef } from "react";
export const Home = () => {
  const ref = useRef(null);
  const { md } = Grid.useBreakpoint();
  const style = { display: "none" };
  return (
    <section className="home" id="home">
      <Row>
        <Col span={24} className="slider">
          <Carousel ref={ref}>
            <div>
              <img src={slide1} alt="slide1" />
            </div>
            <div>
              <img src={slide2} alt="slide2" />
            </div>
            <div>
              <img src={slide3} alt="slide3" />
            </div>
          </Carousel>
        </Col>

        <div className="title" style={md ? {} : style}>
          <h1 style={{ fontSize: 48 }}>
            Фитнес клуб премиум класса <br /> в Нур-Султане на Печерске
          </h1>
          <p style={{ fontSize: 20 }}>
            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Aperiam itaque quasi impedit maiores maxime ullam aut laudantium. Quae fugit culpa iste, enim consectetur, cum
            sint corporis quibusdam et iusto eum?
          </p>
        </div>
      </Row>
    </section>
  );
};
